<template>
<div>
  <my-logo :title='title'></my-logo>
  <textarea v-model="content"></textarea><br />
  <input type="button" @click="submit" value="" />
</div>
</template>

<script>
import MyLogo from '@/components/Logo'
export default {

  data () {
    return {
      content: 'we2',
      title: ''
    }
  },
  //  - this
  created () {

  },
  //  - DOM
  mounted () {

  },
  methods: {
    submit: function () {
      var that = this
      this.$ajax({
        url: 'api/interface/blogs/add_comment',
        method: 'post',
        data: {
          content: that.content
        }
      }).then(function (data2) {
        console.log(data2.data)
        that.content = '' + data2.data.content
      }, function (error) {
        console.log(error)
      })
    }
  },
  components: {
    MyLogo
  }
}
</script>

<style scoped>
/* @import url(); css */
</style>



