import Vue from 'vue'
import Vuex from 'vuex'
// Vuex
Vue.use(Vuex)
// VueX
const moduleA = {
  state: {
    'province': ''
  },
  mutations: {},
  actions: {},
  getters: {}
}
const moduleB = {
  state: {
    'province': ''
  },
  mutations: {},
  actions: {}
}
const store = new Vuex.Store({
  modules: {
    a: moduleA,
    b: moduleB
  }
}
)
export default store



