<template>
  <div class="child-1">
    <p>in B-child1:</p>
    <p>props: {{ pChild1 }}</p>
    <p>$attrs: {{ $attrs }}</p>
    <hr />
    <!-- Ctest BC  v-on $listeners  -->
    <!-- v-bind $attrsCApropsBprops -->
    <c-child v-bind="$attrs" v-on="$listeners"></c-child>
  </div>
</template>
<script>
import C from './C.vue'
export default {
  props: ['pChild1'],
  data () {
    return {}
  },
  inheritAttrs: false,
  components: {
    CChild: C
  },
  mounted () {
    this.$emit('test1', 'BA')
  }
}
</script>



